<template>
  <div class="text-white text-2xl mx-auto my-10" v-if="true">In Arbeit...</div>
  <div v-else class="flex flex-col">
    <div
      v-if="quagging && !codeFound"
      class="
        relative
        py-3
        pl-4
        pr-10
        leading-normal
        text-yellow-700
        bg-yellow-100
        rounded-lg
        mt-5
        text-base
      "
      role="alert"
    >
      <p>Suche Barcode...</p>
    </div>
    <div v-show="quagging" id="code-scanner" class="">
      <video class="w-60 mb-10" />
      <canvas class="drawingBuffer absolute left-0 top-0" />
    </div>
    <div class="flex space-x-2">
      <input
        v-model="productCode"
        placeholder="Produkt-Code"
        class="
          rounded-3xl
          border border-gray-500
          focus:outline-none focus:ring focus:ring-green-500
          px-2
          py-1
        "
        type="text"
      />
      <button class="rounded bg-green-500" @click="startScanning()">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-6 w-10"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M12 4v1m6 11h2m-6 0h-2v4m0-11v3m0 0h.01M12 12h4.01M16 20h4M4 12h4m12 0h.01M5 8h2a1 1 0 001-1V5a1 1 0 00-1-1H5a1 1 0 00-1 1v2a1 1 0 001 1zm12 0h2a1 1 0 001-1V5a1 1 0 00-1-1h-2a1 1 0 00-1 1v2a1 1 0 001 1zM5 20h2a1 1 0 001-1v-2a1 1 0 00-1-1H5a1 1 0 00-1 1v2a1 1 0 001 1z"
          />
        </svg>
      </button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, Ref, ref } from 'vue';
import Quagga from '@ericblade/quagga2';

export default defineComponent({
  name: 'Verkauf',
  setup() {
    const cart: Ref<Array<{ productCode: string }>> = ref([]);
    const productCode = ref('');
    const codeFound = ref(false);
    const quagging = ref(false);

    const startScanning = () => {
      quagging.value = true;
      Quagga.init(
        {
          locate: true,
          frequency: 5,
          numOfWorkers: 2,
          locator: { halfSample: true, patchSize: 'medium' },
          inputStream: {
            name: 'Live',
            type: 'LiveStream',
            target: document.getElementById('code-scanner') || undefined,
          },
          decoder: {
            readers: ['ean_reader'],
          },
        },
        (err) => {
          if (err) {
            throw new Error(err);
          }
          Quagga.start();
        }
      );
    };

    const addProduct = () => {
      if (productCode.value !== '') {
        cart.value.push({ productCode: productCode.value });
      }
    };

    Quagga.onProcessed((data) => {
      if (data && data.codeResult && data.codeResult.code) {
        codeFound.value = true;
      }
    });

    Quagga.onDetected((data) => {
      productCode.value = data.codeResult.code ? data.codeResult.code : '';
      quagging.value = false;
      Quagga.stop();
    });

    return { startScanning, addProduct, productCode, quagging, codeFound };
  },
});
</script>
